import * as React from "react";
import Img from "gatsby-image";
import { graphql, useStaticQuery } from "gatsby";
import { QuoteIcon } from "../icons/QuoteIcon";

const QuoteComponent: React.FC = () => {
  const data = useStaticQuery(graphql`
    query {
      quoteImage: file(relativePath: { eq: "quote-image.jpg" }) {
        name
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `);
  const quoteImage = data.quoteImage;

  return (
    <div className="relative h-96">
      <Img
        fluid={quoteImage.childImageSharp.fluid}
        alt={quoteImage.name}
        className="!absolute z-[-1] w-full h-full !object-cover !object-center"
      />
      <div className="blur-background p-16 absolute right-0 bottom-0">
        <QuoteIcon className="h-8 w-8" />
        <span className="block text-xl mt-8">
          The solar system can support a trillion humans. And then
        </span>
        <span className="block text-xl">
          we'd have a thousand Mozarts and a thousand Einsteins.
        </span>
        <span className="block text-xl mt-8">- Jeff Bezos</span>
      </div>
    </div>
  );
};

export default QuoteComponent;
