import { graphql, useStaticQuery } from "gatsby";
import * as React from "react";
import Img from "gatsby-image";
import ContactUsButton from "./ContactUsButton";
import { ArrowLeftIcon } from "../icons/ArrowLeftIcon";
import { ArrowRightIcon } from "../icons/ArrowRightIcon";

const WhatDoWeDoComponent: React.FC = () => {
  const ref = React.createRef<HTMLDivElement>();

  const queryData = useStaticQuery(graphql`
    query {
      _1: file(relativePath: { eq: "01-service.jpg" }) {
        name
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
      _2: file(relativePath: { eq: "02-service.jpg" }) {
        name
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
      _3: file(relativePath: { eq: "03-service.jpg" }) {
        name
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
      _4: file(relativePath: { eq: "04-service.jpg" }) {
        name
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
      _5: file(relativePath: { eq: "05-service.jpg" }) {
        name
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `);

  const data = [
    {
      title: "Project Designing & Layout Finalization",
      body: `We believe in flawless execution and that stems from a fool-proof plan that creates a clear 
      road-map about how the project will be carried out. Our experts carve out a well-thought strategy 
      for you starting from the design and layouts to actionable steps.`,
    },
    {
      title: "Solar Panel Manufacturing",
      body: `Sustainability is essential and so we work every day towards making this world a better place. 
        VPtronics is also a Solar Panel manufacturer which is built not to just save energy but also to 
        reduce the overall carbon footprint.`,
    },
    {
      title: "PV Lab Set-up",
      body: `PV Lab Set-ups can be diverse and our experts understand the need of the hour. We offer nearly 
        every kind of PV testing that test both - current and next-generation PV modules seamlessly. Our 
        solutions also include mobile module testing labs that can be used for field tests of solar power 
        plants.`,
    },
    {
      title: "After-sales Service & Spare-parts Support",
      body: `Our job doesnâ€™t end once you receive your products. The experts at VPtronics are there to help 
        you throughout the process and offer quick resolutions to your queries. Moreover, we also help 
        our esteemed clients with issues related to the spare-parts`,
    },
    {
      title: "Annual Maintenance Contracts",
      body: `The team at VPtronics is all about ensuring you have a delightful experience in whatever our 
        clients opt for. Our annual maintenance contracts help them review and understand every detail 
        about their purchase related to raw materials for solar cells, glass, and frames. `,
    },
  ];

  const next = () => {
    if (ref.current) {
      ref.current.scrollBy({ left: 200 });
    }
  };

  const previous = () => {
    if (ref.current) {
      ref.current.scrollBy({ left: -200 });
    }
  };

  return (
    <div className="py-16 bg-white text-[#262A2D] container" id="services">
      <div className="flex items-center justify-between select-none">
        <h2 className="text-2xl">How do we do?</h2>
        <div className="flex gap-4">
          <ArrowLeftIcon className="w-5 cursor-pointer" onClick={previous} />
          <ArrowRightIcon className="w-5 cursor-pointer" onClick={next} />
        </div>
      </div>
      <div className="mb-10"></div>
      <div
        className="flex flex-nowrap snap-x snap-mandatory overflow-x-auto gap-8 scroll-smooth"
        ref={ref}
      >
        {data.map((item, index) => (
          <div
            key={item.title}
            className="w-72 m-2 snap-start"
            style={{ flex: "0 0 auto" }}
          >
            <Img
              fluid={queryData[`_${index + 1}`].childImageSharp.fluid}
              alt={queryData[`_${index + 1}`].name}
            />
            <h2 className="text-lg h-16 mt-8">{item.title}</h2>
            <span>{item.body}</span>
          </div>
        ))}
      </div>
      <div className="mb-10"></div>
      <ContactUsButton primary={false} />
    </div>
  );
};

export default WhatDoWeDoComponent;
