import { graphql, useStaticQuery } from "gatsby";
import * as React from "react";
import Img from "gatsby-image";
import ContactUsButton from "./ContactUsButton";

const WhatDoWeDoComponent: React.FC = () => {
  const data = useStaticQuery(graphql`
    query {
      image: file(relativePath: { eq: "solar-farm-close-up-view.jpg" }) {
        name
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `);

  const image = data.image;

  return (
    <div className="bg-[#262A2D]" id="about">
      <div className="py-16 container grid grid-cols-1 lg:grid-cols-2 gap-10 lg:gap-0 items-center">
        <div className="pr-0 lg:pr-16">
          <Img fluid={image.childImageSharp.fluid} alt={image.name} />
        </div>
        <div className="text-white">
          <h2 className="text-2xl">What do we do?</h2>
          <div className="mb-10"></div>
          <span>
            VPtronics is ranked as one of the most reliable and trusted supplier
            in Photovoltaic industry as it brings seamless solutions and
            services to its customers. Our range of services include supply of
            equipment and raw-material for solar panel manufacturing, solar PV
            lab set-up that are built with high-end technology to ensure
            future-proof quality in whatever a manufacturer offers forward. Also
            to facilitate our customers we deal in supply of original OEM
            consumable and non-consumable spares-parts as a service support to
            our existing set of customers.
          </span>
          <div className="mb-10"></div>
          <ContactUsButton primary />
        </div>
      </div>
    </div>
  );
};

export default WhatDoWeDoComponent;
