import * as React from "react";
import { ArrowLeftIcon } from "../icons/ArrowLeftIcon";
import { ArrowRightIcon } from "../icons/ArrowRightIcon";

const LandingPageFeaturesComponent: React.FC = () => {
  const [index, setIndex] = React.useState(0);
  const [progress, setProgress] = React.useState(0);
  const data = [
    {
      title: "Equipment",
      body: `Our company specializes in supplying top-of-the-line equipment for manufacturing solar panels. 
      We understand the importance of using durable and reliable equipment in the manufacturing of 
      solar-panels, which is why we only offer products that meet the highest standards of quality and 
      durability. From tabber stringer to laminator, we assure you to provide a turnkey solution to 
      ensure your solar panel project is a success.`,
    },
    {
      title: "Raw Materials",
      body: `We are committed to providing the highest quality raw-materials for manufacturing solar panels. 
      From photovoltaic cells and Solar glass, to Aluminum Frames we have everything you need
      to ensure your solar panel raw-material needs are fulfilled for a smooth operation. Our materials 
      are carefully sourced and rigorously tested and certified to ensure they meet the highest 
      standards of quality and performance.`,
    },
    {
      title: "Quality and Maintenance",
      body: `We understand that the maintenance and upkeep of solar panel equipment is crucial to their
      long-term performance and efficiency. That's why we offer high quality maintenance support 
      and OEM make spare-parts at best market rates and earliest possible delivery timeline. We 
      are one of the largest professional spare parts suppliers to solar-panel manufacturers 
      based in India. Our company guarantees that the equipment spare parts provided 
      meets the requirements of the user, and the spare parts are in place on 
      schedule.Ensure that the equipment spare parts provided by the company is brand 
      new, unused and non-long-term stock, ensuring quality, specifications and performance 
      as per requirements.
      `,
    },
  ];

  const next = () => {
    const newIndex = index + 1 > data.length - 1 ? 0 : index + 1;
    setIndex(newIndex);
    setProgress(0);
  };

  const previous = () => {
    const newIndex = index - 1 < 0 ? data.length - 1 : index - 1;
    setIndex(newIndex);
    setProgress(0);
  };

  React.useEffect(() => {
    const interval = setInterval(() => {
      if (progress >= 100) {
        setProgress(0);
        next();
      } else {
        setProgress(progress + 0.1);
      }
    }, 10);

    return () => {
      clearInterval(interval);
    };
  }, [progress]);

  return (
    <div className="min-h-[250px] lg:min-h-[200px]">
      <div className="flex items-center justify-between select-none">
        <h2 className="text-xl">{data[index].title}</h2>
        <div className="flex gap-4">
          <ArrowLeftIcon className="w-5 cursor-pointer" onClick={previous} />
          <ArrowRightIcon className="w-5 cursor-pointer" onClick={next} />
        </div>
      </div>
      <hr
        className={`h-px my-3 bg-gray-200 border-0`}
        style={{ width: `${progress}%` }}
      />
      <span>{data[index].body}</span>
    </div>
  );
};

export default LandingPageFeaturesComponent;
