import { graphql, useStaticQuery } from "gatsby";
import * as React from "react";
import Img from "gatsby-image";
import Button from "./Button";

const OurSolutionsComponent: React.FC = () => {
  const queryData = useStaticQuery(graphql`
    query {
      _1: file(relativePath: { eq: "01-solution.jpg" }) {
        name
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
      _2: file(relativePath: { eq: "02-solution.jpg" }) {
        name
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
      _3: file(relativePath: { eq: "03-solution.jpg" }) {
        name
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `);

  const data = [
    {
      title: "Equipment",
      body: `Solar PV technologies like PERC, BiFacial, TOPCon, IBC, M3-M6 or M6-M12`,
    },
    {
      title: "Raw Materials",
      body: `High quality materials. Multi busbar, GB and GG type panels, IBC etc`,
    },
    {
      title: "Quality Assurance and Maintenance",
      body: `Turnkey lab setup projects and Mobile module test labs`,
    },
  ];

  return (
    <div className="py-16 bg-white text-[#262A2D] container" id="solutions">
      <h2 className="text-2xl">Our Solutions</h2>
      <div className="mb-10"></div>
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-16">
        {data.map((item, index) => (
          <div
            key={item.title}
            className="w-80 m-2 snap-start"
            style={{ flex: "0 0 auto" }}
          >
            <Img
              fluid={queryData[`_${index + 1}`].childImageSharp.fluid}
              alt={queryData[`_${index + 1}`].name}
            />
            <h2 className="text-lg h-12 mt-8">{item.title}</h2>
            <span>{item.body}</span>
            <div className="mt-8">
              <Button primary={false}>Enquire</Button>
            </div>
          </div>
        ))}
      </div>
      <div className="mb-10"></div>
    </div>
  );
};

export default OurSolutionsComponent;
