import * as React from "react";
import ContactUsButton from "./ContactUsButton";
import LandingPageFeaturesComponent from "./LandingPageFeaturesComponent";

const LandingPage: React.FC = () => {
  return (
    <div className="py-16 container grid grid-cols-1 lg:grid-cols-2 items-end gap-20 lg:gap-0">
      <div>
        <span className="block text-5xl mb-3">Build</span>
        <span className="block text-5xl mb-3">Solar Panels</span>
        <span className="block text-5xl mb-3">With Us.</span>
        <div className="mb-10"></div>
        <span className="block">
          We help you to build, test and distribute high
        </span>
        <span className="block">quality solar panels across the world.</span>
        <div className="mb-10"></div>
        <ContactUsButton primary />
      </div>
      <LandingPageFeaturesComponent />
    </div>
  );
};

export default LandingPage;
