import { Link } from "gatsby";
import * as React from "react";

const Button: React.FC<{ primary: boolean; children: string; to?: string }> = (
  props
) => {
  const children = (props as any).children;
  const primary = props.primary ?? true;
  return (
    <Link
      to={props.to ?? "/contact"}
      className={`text-sm ${
        primary ? `bg-white text-black` : `bg-[#262A2D] text-white`
      } px-6 py-2 rounded-full`}
    >
      {children}
    </Link>
  );
};

export default Button;
