import * as React from "react";
import { graphql, PageProps } from "gatsby";
import Img from "gatsby-image";
import Navbar from "../components/Navbar";
import LandingPage from "../components/LandingPage";
import WhatDoWeDoComponent from "../components/WhatDoWeDoComponent";
import HowDoWeDoComponent from "../components/HowDoWeDoComponent";
import QuoteComponent from "../components/QuoteComponent";
import OurSolutionsComponent from "../components/OurSolutionsComponent";
import Footer from "../components/Footer";
import { SEO } from "../components/SEO";

const IndexPage: React.FC<PageProps> = (props) => {
  const data = props.data as any;
  const backgroundImage = data.backgroundImage;
  return (
    <main>
      <div className="relative scroll-smooth">
        <Img
          fluid={backgroundImage.childImageSharp.fluid}
          alt={backgroundImage.name}
          className="!absolute z-[-1] w-full h-full !object-cover !object-center"
        />
        <div className="blur-background">
          <Navbar />
        </div>
        <div className="mt-40 blur-background">
          <LandingPage />
        </div>
      </div>
      <OurSolutionsComponent />
      <WhatDoWeDoComponent />
      <HowDoWeDoComponent />
      <QuoteComponent />
      <Footer />
    </main>
  );
};

export default IndexPage;

export const Head = () => <SEO />;

export const query = graphql`
  query {
    backgroundImage: file(relativePath: { eq: "solar-farm.jpg" }) {
      name
      childImageSharp {
        fluid {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`;
